// Toast.jsx
import React from 'react';
import PropTypes from 'prop-types';
import { Alert, Collapse } from 'react-bootstrap';

class Toast extends React.Component {
  componentDidUpdate() {
    if (this.props.showing) {
      clearTimeout(this.dimissTimer);
      this.dimissTimer = setTimeout(this.props.onDismiss, 5000);
    }
  }

  componentWillUnamount() {
    clearTimeout(this.dimissTimer);
  }

  render() {
    return (
      <Collapse in={this.props.showing}>
        <div style={{ position: 'fixed', top: 30, left: 0, right: 0, textAlign: 'center' }}>
          <Alert 
            style={{ display: 'inline-block', width: 500 }}
            bsStyle={this.props.bsStyle}
            onDismiss={this.props.onDismiss}
          >
            {this.props.message}
          </Alert>
        </div>
      </Collapse>
    );
  }
}

Toast.propTypes = {
  showing: PropTypes.bool.isRequired,
  onDismiss: PropTypes.func.isRequired,
  bsStyle: PropTypes.string,
  message: PropTypes.string.isRequired,
};

Toast.defaultProps = {
  bsStyle: 'success',
};

export default Toast;
